import PropTypes from 'prop-types';
import { useRef } from 'react';
import { useNavigate } from 'react-router';
import Slider from 'react-slick';
// @mui
import { useTheme, styled } from '@mui/material/styles';
import { Box, Stack, Paper, Button, Avatar, Container, Typography } from '@mui/material';

// components
import Image from '../../components/Image';
import Label from '../../components/Label';
import { CarouselArrows } from '../../components/carousel';
import TextMaxLine from '../../components/TextMaxLine';
import { MotionInView, varFade } from '../../components/animate';
// utils
import { fDateTime } from '../../utils/formatTime';
import cssStyles from '../../utils/cssStyles';
// mock 
import _mock, { _carouselsMembers } from '../../_mock';

// ----------------------------------------------------------------------
const recommendStreams = [...Array(10)].map((_, index) => ({
  cover: _mock.image.cover(index),
  title: _mock.text.title(index),
  name: _mock.name.fullName(index),
  avatar: _mock.image.avatar(index),
  id: _mock.id(index),
  active: "passed",
  date: _mock.time(index),

}));

// ----------------------
const BottomOverWidget = styled('div')(({ theme }) => ({
  ...cssStyles(theme).bgBlur(),
  position: 'absolute', bottom: 0, left: 0, right: 0, zIndex: 99,
  display: 'flex', alignItems: 'center',
  padding: theme.spacing(1, 2),
  backgroundColor: theme.palette.grey[1000_40]
}));

// ----------------------------------------------------------------------

export default function LanRecommendStreams() {
  const carouselRef = useRef(null);
  const theme = useTheme();
  const navigate = useNavigate();
  const onCardClick = (item, index) => {
    
    if (item.type === "product")
      navigate(`/live-ecommerce/product/${item.id}`);
    else
      navigate(`/live-ecommerce/liveshow/${item.id}`);

  }


  const settings = {
    arrows: false,
    slidesToShow: 4,
    centerMode: true,
    centerPadding: '0px',
    rtl: Boolean(theme.direction === 'rtl'),
    responsive: [
      {
        breakpoint: 1279,
        settings: { slidesToShow: 3 },
      },
      {
        breakpoint: 959,
        settings: { slidesToShow: 2 },
      },
      {
        breakpoint: 600,
        settings: { slidesToShow: 1 },
      },
    ],
  };

  const handlePrevious = () => {
    carouselRef.current.slickPrev();
  };

  const handleNext = () => {
    carouselRef.current.slickNext();
  };

  return (
    <Paper sx={{ py: 1, my:4}}>
      <MotionInView variants={varFade().inUp}>
        <Typography variant="h4" sx={{ mb: 1 ,pl:3}}>
          Recommend Streams
        </Typography>
      </MotionInView>
      <Box sx={{ position: 'relative' }}>
        <CarouselArrows filled onNext={handleNext} onPrevious={handlePrevious}>
          <Slider ref={carouselRef} {...settings}>
            {recommendStreams.map((item, index) => (
              <ShowCard key={index} item={item}
                handleClick={() => onCardClick(item, index)}
              />

            ))}
          </Slider>
        </CarouselArrows>
      </Box>
    </Paper>
  );
}

// ----------------------------------------------------------------------

ShowCard.propTypes = {
  handleClick: PropTypes.func,
  item: PropTypes.shape({
    avatar: PropTypes.string,
    name: PropTypes.string,
    cover: PropTypes.string,
    title: PropTypes.string,
    active: PropTypes.string,
  }),
};


function ShowCard({ item }) {
  const theme = useTheme();
  const { avatar, name, title, cover, date, active } = item;
  // const router =useRouter();
  return (
    <Paper sx={{
      mx: 1.5, my: 2, borderRadius: 1, bgcolor: 'background.neutral', position: 'relative', overflow: 'hidden',
      border:'1px solid lightgrey',
      '&:hover': { boxShadow: `0px 4px 10px 3px ${theme.palette.grey[500_80]}` }
    }}>
      <Image src={cover} ratio="5/4" sx={{ borderRadius: 1 }} />
      <Box
        sx={{
          left: 10, zIndex: 9, top: 10, position: 'absolute',
          bgcolor: (active === 'live') ? 'primary.main' : ((active === 'upcoming') ? 'grey.50056' : 'grey.100080'),
          color: 'white', borderRadius: 1, px: 1, fontSize: 12,
          // border: (active === 'upcoming') ? 'solid 1px error' : '',
        }} >
        {active === 'live' ? "Live" : fDateTime(date)}
      </Box>

      <BottomOverWidget >
        <Stack direction="row" alignItems="center" spacing={1}>
          <Avatar alt={name} src={avatar} sx={{ border: '2px solid white' }} />
          <div>
            <TextMaxLine sx={{ color: '#fff !important', lineHeight: 1 }} variant={"body2"} line={2}>{title}</TextMaxLine>
            <Typography sx={{ color: '#fff', display: 'block', px: 1 }} variant={"caption"}>
              {name}
            </Typography>
          </div>
        </Stack>
      </BottomOverWidget>
    </Paper>
  );
}
